@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Roboto", sans-serif;
}

.wrapper {
  width: 700px;
  background-color: white;
  border-radius: 10px;
  padding: 4rem;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
}

@media (max-width: 800px) {
  .wrapper {
    width: 100%;
    padding: 2rem;
  }
}

.container {
  min-height: auto;
}

.title {
  text-align: center;
  color: white;
  padding: 2rem;
  font-size: 1.8rem;
}

@media (max-width: 800px) {
  .title {
    font-size: 1.4rem;
  }
}

#text {
  text-align: start;
  font-size: 2rem;
}

@media (max-width: 800px) {
  #text {
    font-size: 1.6rem;
  }
}

#author {
  font-size: 1.4rem;
  text-align: end;
}

@media (max-width: 800px) {
  #author {
    font-size: 1.2rem;
  }
}

.btn {
  padding: 0.4rem 0.8rem;
  border: none;
  font-size: 1.2rem;
  white-space: nowrap;
}

@media (max-width: 800px) {
  .btn {
    font-size: 1rem;
  }
}

.btn:focus {
  box-shadow: none;
}

#new-quote {
  text-align: end;
}

.by {
  text-align: center;
  color: white;
  padding: 2rem;
  font-size: 1.2rem;
}

@media (max-width: 800px) {
  .by {
    font-size: 1rem;
  }
}

